/* ==========================================================================
   Design System
   Button Styling
   ========================================================================== */
.a-btn {
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  display: inline-block;
  box-sizing: border-box;
  padding: 0.5em 0.875em;
  border: 0;
  margin: 0;
  border-radius: 0.25em;
  cursor: pointer;
  font-size: 1em;
  font-weight: 500;
  line-height: normal;
  text-align: center;
  text-decoration: none;
  transition: background-color 0.1s;
}
.a-btn,
.a-btn:link,
.a-btn:visited {
  background-color: #0072ce;
  color: #ffffff;
}
.a-btn:hover,
.a-btn.hover,
.a-btn:focus,
.a-btn.focus {
  background-color: #0050b4;
}
.a-btn:focus,
.a-btn.focus {
  outline: 1px dotted #0072ce;
  outline-offset: 1px;
}
.a-btn:active,
.a-btn.active {
  background-color: #254b87;
}
button.a-btn::-moz-focus-inner,
input.a-btn::-moz-focus-inner {
  border: 0;
}
.a-btn__secondary,
.a-btn__secondary:link,
.a-btn__secondary:visited {
  background-color: #5a5d61;
  color: #ffffff;
}
.a-btn__secondary:hover,
.a-btn__secondary.hover,
.a-btn__secondary:focus,
.a-btn__secondary.focus {
  background-color: #43484e;
}
.a-btn__secondary:focus,
.a-btn__secondary.focus {
  outline-color: #5a5d61;
}
.a-btn__secondary:active,
.a-btn__secondary.active {
  background-color: #101820;
}
.a-btn__warning,
.a-btn__warning:link,
.a-btn__warning:visited {
  background-color: #c3381c;
  color: #ffffff;
}
.a-btn__warning:hover,
.a-btn__warning.hover,
.a-btn__warning:focus,
.a-btn__warning.focus {
  background-color: #b63014;
}
.a-btn__warning:focus,
.a-btn__warning.focus {
  outline-color: #c3381c;
}
.a-btn__warning:active,
.a-btn__warning.active {
  background-color: #43484e;
}
.a-btn__disabled,
.a-btn[disabled],
.a-btn__disabled:link,
.a-btn[disabled]:link,
.a-btn__disabled:visited,
.a-btn[disabled]:visited,
.a-btn__disabled:hover,
.a-btn[disabled]:hover,
.a-btn__disabled.hover,
.a-btn[disabled].hover,
.a-btn__disabled:focus,
.a-btn[disabled]:focus,
.a-btn__disabled.focus,
.a-btn[disabled].focus,
.a-btn__disabled:active,
.a-btn[disabled]:active,
.a-btn__disabled.active,
.a-btn[disabled].active {
  background-color: #d2d3d5;
  color: #43484e;
  cursor: default;
  cursor: not-allowed;
}
.a-btn__disabled:focus,
.a-btn[disabled]:focus,
.a-btn__disabled.focus,
.a-btn[disabled].focus {
  outline-color: #d2d3d5;
}
@media only all and (max-width: 37.5em) {
  .a-btn__full-on-xs {
    display: block;
    width: 100%;
  }
}
.a-btn__link {
  padding: 0;
  padding-bottom: 1.5px;
  border-radius: 0;
  text-decoration-line: underline;
  text-decoration-thickness: 1px;
  text-decoration-style: dotted;
  text-underline-offset: 4.5px;
}
.a-btn__link,
.a-btn__link:link,
.a-btn__link:visited {
  text-decoration-color: #0072ce;
  background-color: transparent;
  color: #0072ce;
}
.a-btn__link:hover,
.a-btn__link.hover {
  text-decoration-color: #0050b4;
  text-decoration-style: solid;
  background-color: transparent;
  color: #0050b4;
}
.a-btn__link:focus,
.a-btn__link.focus {
  text-decoration-style: solid;
  background-color: transparent;
  outline: 1px dotted #0072ce;
}
.a-btn__link:active,
.a-btn__link.active {
  text-decoration-color: #254b87;
  text-decoration-style: solid;
  background-color: transparent;
  color: #254b87;
}
.a-btn__link.a-btn__secondary,
.a-btn__link.a-btn__secondary:link,
.a-btn__link.a-btn__secondary:visited {
  text-decoration-color: #5a5d61;
  background-color: transparent;
  color: #5a5d61;
}
.a-btn__link.a-btn__secondary:hover,
.a-btn__link.a-btn__secondary.hover {
  text-decoration-color: #43484e;
  color: #43484e;
}
.a-btn__link.a-btn__secondary:focus,
.a-btn__link.a-btn__secondary.focus {
  outline-color: #5a5d61;
}
.a-btn__link.a-btn__secondary:active,
.a-btn__link.a-btn__secondary.active {
  text-decoration-color: #101820;
  color: #101820;
}
.a-btn__link.a-btn__warning,
.a-btn__link.a-btn__warning:link,
.a-btn__link.a-btn__warning:visited {
  text-decoration-color: #c3381c;
  background-color: transparent;
  color: #c3381c;
}
.a-btn__link.a-btn__warning:hover,
.a-btn__link.a-btn__warning.hover {
  text-decoration-color: #b63014;
  color: #b63014;
}
.a-btn__link.a-btn__warning:focus,
.a-btn__link.a-btn__warning.focus {
  outline-color: #c3381c;
}
.a-btn__link.a-btn__warning:active,
.a-btn__link.a-btn__warning.active {
  text-decoration-color: #43484e;
  color: #43484e;
}
.a-btn_icon__on-left {
  padding-right: 0.6875em;
  border-right: 1px solid #80b9e7;
  margin-right: 0.4375em;
}
.a-btn_icon__on-right {
  padding-left: 0.6875em;
  border-left: 1px solid #80b9e7;
  margin-left: 0.4375em;
}
.a-btn__secondary .a-btn_icon {
  border-color: #adaeb0;
}
.a-btn__warning .a-btn_icon {
  border-color: #e19c8e;
}
.a-btn__disabled .a-btn_icon,
.a-btn[disabled] .a-btn_icon {
  border-color: #8b8e92;
}
.m-btn-group .a-btn + .a-btn {
  margin-left: 0.375em;
}
